@use 'global' as *;

.cafe {
  margin-bottom: rem(95);
}

.cafe__inner {
  max-width: rem(1450);
  padding: 0 var(--padding-pc);
  margin-inline: auto;
}

.cafe__header {
  position: relative;
  width: 100%;
}

.cafe__logo {
  width: rem(185);
  aspect-ratio: 1;
  margin-inline: auto;
}

.cafe__logo-img {
  object-fit: contain;
}

.cafe__title-en {
  margin-top: rem(-110);
  margin-bottom: rem(40);
  font-family: var(--en-font);
  font-size: rem(28);
  font-weight: var(--bold);
  color: var(--brown);
  letter-spacing: 0.5em;
  line-height: 2;
  text-align: center;
}

.cafe__title-jp {
  font-family: var(--base-font-family);
  font-size: rem(22);
  font-weight: var(--bold);
  color: var(--brack);
  letter-spacing: 0.18em;
  line-height: 2;
  text-align: center;
}

.cafe__text {
  width: rem(450);
  margin-inline: auto;
  margin-bottom: rem(80);
  text-align: center;
  font-family: var(--base-font-family);
  font-size: rem(16);
  font-weight: var(--regular);
  color: var(--brack);
  letter-spacing: 0.12em;
  line-height: 2;
}

.cafe__menu {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: rem(50);
  margin-bottom: rem(50);
}

.cafe__menu-item {
  aspect-ratio: 432/289;
}

.cafe__more {
  width: fit-content;
  display: block;
  margin-left: auto;
  margin-right: rem(45);
  font-family: var(--en-font);
  font-size: rem(22);
  font-weight: var(--bold);
  color: var(--brack);
  letter-spacing: rem(2.64);
  line-height: 2;
  position: relative;
  padding-bottom: rem(3);
  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 50%);
    bottom: 0;
    aspect-ratio: 1;
    width: rem(10);
    border-radius: 50%;
    background-color: var(--brown);
  }
}

.cafe__marche {
  position: absolute;
  left: rem(254);
  top: rem(1166);
  width: rem(888);
  height: rem(346);
}

.cafe__marche-badge {
  position: absolute;
  left: rem(339);
  top: 0;
  width: rem(210);
  height: rem(32);
  background-color: var(--brown);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.cafe__marche-badge::before {
  content: '出展者募集中';
  font-family: var(--base-font-family);
  font-size: rem(16);
  font-weight: var(--bold);
  color: var(--milktea);
  letter-spacing: rem(2.4);
}

.cafe__marche-box {
  position: absolute;
  left: 0;
  top: rem(24);
  width: rem(888);
  height: rem(322);
  padding: rem(55.5) rem(80);
  background-color: var(--milktea);
  border: 1px solid var(--brown);
  box-sizing: border-box;
}

.cafe__marche-title {
  font-family: var(--base-font-family);
  font-size: rem(24);
  font-weight: var(--bold);
  color: var(--brack);
  letter-spacing: rem(12);
  line-height: 2;
  margin: 0 0 rem(68);
  text-align: center;
}

.cafe__marche-text {
  font-family: var(--base-font-family);
  font-size: rem(16);
  font-weight: var(--regular);
  color: var(--brack);
  letter-spacing: rem(1.92);
  line-height: 2;
  margin: 0 0 rem(68);
  text-align: center;
  width: rem(728);
}

.cafe__marche-link {
  position: absolute;
  left: rem(842);
  top: rem(258);
  width: rem(88);
  height: rem(88);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--white);
  border: 1px solid var(--brown);
  border-radius: 50%;
  text-decoration: none;
}

.cafe__marche-icon {
  width: rem(54);
  height: rem(54);
  object-fit: contain;
}

@include mq {
  .cafe {
    position: static;
    width: 100%;
    padding: rem(60) 0;
  }

  .cafe__inner {
    padding: 0 var(--padding-sp);
  }

  .cafe__header {
    margin-bottom: rem(40);
  }

  .cafe__logo {
    position: static;
    width: rem(120);
    height: rem(120);
    margin: 0 auto rem(20);
  }

  .cafe__title-en {
    position: static;
    font-size: rem(24);
    transform: none;
    margin-bottom: rem(20);
  }

  .cafe__title-jp {
    position: static;
    font-size: rem(18);
    transform: none;
    margin-bottom: rem(20);
  }

  .cafe__text {
    position: static;
    font-size: rem(14);
    transform: none;
    width: 100%;
    margin-bottom: rem(40);
  }

  .cafe__menu {
    position: static;
    width: 100%;
    height: auto;
    margin-bottom: rem(40);
  }

  .cafe__menu-item {
    position: static;
    margin-bottom: rem(20);
    width: 100%;
    height: auto;
    mask: none;
  }

  .cafe__menu-item:nth-of-type(1) {
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
  }

  .cafe__menu-item:nth-of-type(2) {
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
  }

  .cafe__menu-item:nth-of-type(3) {
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
  }

  .cafe__menu-img {
    height: auto;
  }

  .cafe__more {
    position: static;
    text-align: center;
    margin-bottom: rem(40);
  }

  .cafe__more::after {
    display: none;
  }

  .cafe__marche {
    position: static;
    width: 100%;
    height: auto;
    margin-bottom: rem(40);
  }

  .cafe__marche-badge {
    position: static;
    width: 100%;
    margin-bottom: rem(20);
  }

  .cafe__marche-box {
    position: static;
    width: 100%;
    height: auto;
    padding: rem(30) rem(20);
  }

  .cafe__marche-title {
    font-size: rem(20);
    margin-bottom: rem(20);
  }

  .cafe__marche-text {
    font-size: rem(14);
    width: 100%;
    margin-bottom: rem(20);
  }

  .cafe__marche-link {
    position: static;
    margin: 0 auto;
  }
}
